    width: 100%;
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 30px;
    padding-bottom: 30px;
  }
  .partners__item:nth-child(1) img {
    max-width: 150px;
    height: auto;
  }
}

.partners__item:last-child {
  margin-right: 0;
}

.partners__item img {
  z-index: 1;
  /* adaptive */
  /* adaptive */
}

@media only screen and (max-width: 960px) {
  .partners__item img {
    width: 100%;
  }
}

@media only screen and (max-width: 720px) {
  .partners__item img {
    width: auto;
  }
}

@media only screen and (max-width: 480px) {
  .partners__item img {
    height: 50px;
  }
}

.partners__item:after {
  width: 100%;
  height: 100%;
  background: #2F41FF;
  transition: opacity ease-in-out 0.4s;
  opacity: 0;
}

.partners__item:hover:after {
  opacity: 1;
}

.about {
  position: relative;
  overflow: hidden;
  background-color: #fff;
  padding-top: 130px;
  padding-bottom: 350px;
}

.about .container {
  z-index: 2;
}

.about__container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding-left: 165px;
  padding-right: 165px;
}

.about__description {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.about__header {
  text-align: center;
  opacity: 0;
  transform: translateY(-100px);
  transition: opacity ease-in-out 1s 0s, transform ease-in-out 1s 0s;
}

.about__header.vi {
  opacity: 1;
  transform: translateY(0);
}

.about__backword {
  text-shadow: 0px 37px 65px rgba(0, 0, 0, 0.0599323);
  z-index: -1;
  width: 2100px;
  bottom: 0;
  transform: translate(500px, 250px);
  opacity: .7;
}

.about__info {
  margin-left: 40px;
  margin-right: 90px;
  margin-top: 120px;
  opacity: 0;
  transform: translateY(-100px);
  transition: opacity ease-in-out 1s 0.8s, transform ease-in-out 1s 0.8s;
}

.about__info.vi {
  opacity: 1;
  transform: translateY(0);
}

.about__name {
  font-size: 30px;
  line-height: 33px;
  color: #2F41FF;
  width: 225px;
  font-family: 'Montserrat-Bold', serif;
  font-weight: 500;
  font-weight: 500;
}

.about__position {
  font-size: 14px;
  line-height: 33px;
  color: #323941;
  margin-top: 5px;
}

.about__hero {
  margin-top: -15px;
  flex-shrink: 0;
  position: relative;
}

.about__hero img {
  transform: translateY(-100px);
  opacity: 0;
  transition: opacity ease-in-out 1s 0.6s, transform ease-in-out 1s 0.6s;
}

.about__hero img.vi {
  transform: translateY(0);
  opacity: 1;
}

.about__text {
  font-size: 14px;
  line-height: 28px;
  color: #323941;
  width: 337px;
  margin-top: 40px;
  transform: translateY(-100px);
  opacity: 0;
  transition: opacity ease-in-out 1s 1.0s, transform ease-in-out 1s 1.0s;
}

.about__text.vi {
  opacity: 1;
  transform: translateY(0);
}

.about__text svg {
  font-size: 74px;
  opacity: .1;
  margin-bottom: 18px;
}

.about__circle {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 449px;
  height: 449px;
  box-shadow: 0px 97px 131px rgba(43, 51, 78, 0.0870302);
  border-radius: 100%;
}

.about__circle_c1 {
  transform: scale(0.5) translate(-15px, 10px);
  transform-origin: left center;
}

.about__circle_c2 {
  transform: scale(0.5) translate(-10px, 10px);
}
