
@include font(Montserrat-Bold, Montserrat-Bold);
@mixin fb {
  font-family: 'Montserrat-Bold', serif;
  font-weight: 500; }

@include font(Montserrat-ExtraBold, Montserrat-ExtraBold);
@mixin feb {
  font-family: 'Montserrat-ExtraBold', serif; }

@include font(Montserrat-Medium, Montserrat-Medium);
@mixin fm {
  font-family: 'Montserrat-Medium', serif; }

@include font(Montserrat-Regular, Montserrat-Regular);
@mixin fr {
  font-family: 'Montserrat-Regular', serif; }

@include font(Montserrat-SemiBold, Montserrat-SemiBold);
@mixin fsb {
  font-family: 'Montserrat-SemiBold', serif; }

@include font(Montserrat-Black, Montserrat-Black);
@mixin fblack {
  font-family: 'Montserrat-Black', serif; }


$accent: #2F41FF;
$black: #323941;
body {
	@include fr;
	transition: opacity ease-in-out .4s;
	background-color: #0E1114;
	width: 100%;
	&.loaded {
		opacity: 1!important; } }
.out {
	width: 100%;
	overflow-x: hidden;
	padding-bottom: 1000px; }
h1,h2,h3,h4,p {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }
a, a:visited, a:hover, a:focus {
	color: inherit;
	text-decoration: none; }
* {
	max-height: 100000000px;
	box-sizing: border-box; }

h1,h2,h3,h4 {
	@include feb; }
h2 {
	font-size: 40px;
	color: $black;
	text-transform: uppercase;
	letter-spacing: 1px;
	/* adaptive */
	@include below(720px) {
		font-size: 28px; }
	@include below(480px) {
		font-size: 24px; }
	@include below(340px) {
		font-size: 20px; }
	/* adaptive */ }

.container {
	padding-left: 215px;
	padding-right: 215px;
	position: relative;
	width: 100%;
	max-width: 1903px;
	@include flexbox();
	flex-direction: column;
	/* adaptive */
	@include below(1100px) {
		padding-left: 50px;
		padding-right: 50px; }
	@include below(720px) {
		margin-top: 0;
		padding-top: 50px;
		padding-left: 20px;
		padding-right: 20px; }
	/* adaptive */ }
.button {
	span {
		@include fsb;
		font-size: 12px;
		color: #fff; }
	padding-top: 15px;
	padding-bottom: 15px;
	padding-left: 50px;
	padding-right: 50px;
	border-radius: 27px;
	overflow: hidden;
	border: solid 1px #4958FF;
	cursor: pointer;
	background-color: $accent;
	box-shadow: 0px 16px 19px rgba(35, 41, 85, 0.256283);
	@include pseudo;
	&:after {
		width: 100%;
		height: 100%;
		z-index: 0;
		background-color: #4958FF;
		opacity: 0;
		transition: opacity ease-in-out .2s;
		border-radius: 27px; }

	span {
		position: relative;
		z-index: 1; }
	&:hover {
		&:after {
			opacity: 1; } } }
.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	@include flexbox(flex-start, space-between);
	padding-right: 82px;
	padding-left: 64px;
	padding-top: 41px;
	z-index: 10;
	color: #fff;
	/* animation */
	transform: translateY(-100%);
	transition: transform ease-in-out 1s 2.5s, padding-top ease-in-out .2s, padding-bottom ease-in-out .2s;
	&.loaded {
		transform: translateY(0); }
	/* animation */
	/* adaptive */
	@include below(1100px) {
		padding-right: 47px; }
	@include below(480px) {
		padding-right: 25px;
		padding-left: 25px; }
	/* adaptive */
	&__left {
		@include flexbox(center, space-between);
		/* adaptive */
		@include below(720px) {
			height: 54px; }
		/* adaptive */ }
	&__logo svg {
		width: 117px;
		/* adaptive */
		@include below(480px) {
			width: 80px; }
		/* adaptive */ }
	&__phone {
		@include fm;
		font-size: 22px;
		/* adaptive */
		@include below(480px) {
			font-size: 18px; }
		/* adaptive */ }

	&__lead {
		margin-left: 60px;
		/* adaptive */
		@include below(720px) {
			display: none; }
		/* adaptive */ }
	@include pseudo;
	&:after {
		width: 100%;
		height: 100%;
		background-color: #fff;
		z-index: -1;
		opacity: 0;
		transition: opacity ease-in-out .2s; }
	&.scrolled {
		& {
			color: #000;
			padding-bottom: 10px;
			padding-top: 10px; }

		&:after {
			opacity: 1; } } }
section {
	@include flexbox(center, flex-start, column); }
.main {
	z-index: 6;
	&__container {
		@include flexbox(flex-start, flex-start, column);
		align-self: flex-start;
		padding-top: 281px;
		padding-left: 375px;
		/* animation */
		>* {
			opacity: 0;
			transition: opacity ease-in-out 1s;
			@include durationi(500ms, 2, 2600ms); }
		&.loaded {
			>* {
				opacity: 1; } }
		/* animation */

		/* adaptive */
		@include below(1910px) {
			padding-left: 300px; }
		@include below(1450px) {
			padding-top: 190px; }
		@include below(1100px) {
			padding-left: 55px;
			margin-top: 180px; }
		@include below(720px) {
			padding-top: 200px; }
		@include below(480px) {
			padding-left: 20px;
			padding-right: 20px;
			padding-top: 150px; }
		/* adaptive */ }
	&__header {
		font-size: 50px;
		letter-spacing: -.8px;
		/* adaptive */
		@include below(1500px) {
			font-size: 45px; }
		@include below(1400px) {
			font-size: 40px; }
		@include below(1240px) {
			font-size: 30px; }
		@include below(1100px) {
			font-size: 35px; }
		@include below(720px) {
			font-size: 30px;
			padding-right: 20px; }
		@include below(480px) {
			font-size: 25px; }
		/* adaptive */
		line-height: 1.26;
		color: #fff;
		text-transform: uppercase;
		&_accent {
			color: $accent; } }
	&__button {
		margin-top: 54px;
		padding-left: 70px;
		padding-right: 65px;
		/* adaptive */
		@include below(720px) {
			margin-top: 30px; }
		/* adaptive */ }
	position: relative;
	&__background {
		position: absolute;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		z-index: -1;
		transform: translateY(-400px);
		background-image: url("../img/hero-bg.jpg");
		background-size: cover;
		/* animation */
		opacity: 0;
		transition: opacity ease-in-out 1s 1.5s;
		&.loaded {
			opacity: 1; }
		/* animation */
		/* adaptive */
		@include below(720px) {
			transform: translateY(0px); }
		/* adaptive */ } }
.navigation {
	color: #fff;
	position: fixed;
	top: 292px;
	left: 54px;

	/* adaptive */
	@include below(1450px) {
		top: 200px; }
	@include below(1100px) {
		left: auto;
		right: 54px; }
	@include below(960px) {
		display: none; }
	/* adaptive */
	&__elem {
		display: block;
		margin-bottom: 34px;
		font-size: 12px;
		/* adaptive */
		@include below(1100px) {
			display: flex;
			justify-content: flex-end; }
		/* adaptive */
		&.active {
			color: #82B1E2; } }
	&__name {
		margin-left: 6px; }
	&__num {
		/* adaptive */
		@include below(1100px) {
			order: 1;
			margin-left: 6px; }
		/* adaptive */ } }
.projects {
	margin-bottom: -120px;
	margin-top: 245px;
	z-index: 2;
	/* adaptive */
	@include below(480px) {
		margin-top: 0px;
		margin-bottom: 30px;
		.container {
			margin-top: 0; } }
	/* adaptive */
	&__header {
		margin-top: 100px;
		margin-bottom: 90px;
		opacity: 0;
		transform: translateY(100px);
		color: #fff;
		/* adaptive */
		@include below(1500px) {
			margin-top: 60px;
			margin-bottom: 50px; }
		@include below(720px) {
			margin-top: 40px;
			margin-bottom: 30px;
			transition: opacity 0.4s ease-in-out, transform 0.4s ease-in-out;
			transform: translateY(0px);
			&.vi {
				opacity: 1;
				transform: translateY(0px); } }
		/* adaptive */ }
	&__wrapper {
		padding-left: 148px;
		padding-right: 148px;
		padding-bottom: 128px;
		z-index: 2;
		@include flexbox(center, center);
		flex-wrap: wrap;
		opacity: 0;
		transform: translateY(100px);
		/* adaptive */
		@include below(1650px) {
			padding-left: 110px;
			padding-right: 110px; }
		@include below(720px) {
			padding-left: 50px;
			padding-right: 50px;
			transition: opacity 0.4s ease-in-out, transform 0.4s ease-in-out;
			transform: translateY(0px);
			&.vi {
				opacity: 1;
				transform: translateY(0px); } }
		@include below(480px) {
			padding-left: 20px;
			padding-right: 20px;
			padding-bottom: 20px; }
		/* adaptive */ }

	&__background {
		backdrop-filter: blur(50px);
		@include flexbox(center, center, column);
		position: relative;
		@include below(480px) {
			border-radius: 30px; } }
	&__item {
		width: calc(100%/3 - 12px);
		height: 380px;
		margin-bottom: 10px;
		margin-right: 12px;
		/* adaptive */
		@include below(1650px) {
			height: 300px; }
		@include below(1500px) {
			height: 260px; }
		@include below(960px) {
			width: calc(50% - 5px);
			margin-right: 0;
			&:nth-child(2n) {
				margin-left: 10px; } }
		@include below(720px) {
			height: 200px; }
		@include below(480px) {
			height: 150px; }
		/* adaptive */

		img {
			width: 100%;
			height: 100%;
			object-fit: cover; }
		&.no_margin {
			margin-right: 0; } }
	&__cifre {
		position: absolute;
		@include fblack;
		font-size: 490px;
		color: #26282D;
		opacity: 0.81;
		text-shadow: 0px 37px 65px rgba(0, 0, 0, 0.0599323);
		right: 0%;
		top: 50%;
		transform: translate(200px, calc(-50% + 50px));
		opacity: 0;
		// adaptive
		@include below(480px) {
			font-size: 300px; }
 } }		// adaptive
.lines {
	z-index: 0;
	&__wrapper {
		@include flexbox(flex-start, space-between);
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		padding-left: 215px;
		padding-right: 215px;
		/* animation */
		>* {
			transform: scaleY(0.00001);
			transform-origin: top left;
			transition: transform ease-in-out 1s;
			@include durationi(300ms, 6, 300ms); }
		&.loaded, &.vi {
			>* {
				transform: scaleY(1); } }
		/* animation */
		/* adaptive */
		@include below(1100px) {
			padding-left: 50px;
			padding-right: 50px; }
		/* adaptive */ }
	&__line {
		top: 0;
		height: 940px;
		width: 1px;
		background-color: #AEAEAE;
		opacity: .15;
		position: relative;
		&_l2, &_l5 {
			@include below(720px) {
				display: none; } }
		&_l2 {
			left: -9%; }
		&_l3 {
			left: -5.7%; }
		&_l4 {
			left: 11%; }
		&_l5 {
			left: 6%; }
		&_whites {
			background-color: #D8D8D8;
			opacity: 0.59; }
		&_gray {
			background-color: #F3F3F3;
			opacity: 1; } } }

.backword {
	position: absolute;
	@include fblack;
	font-size: 300px;
	color: #fff;
	opacity: 0;
	z-index: 1;
	// animation
	@include below(720px) {
		transition: transform 4s ease-in-out, opacity 4s ease-in-out;
		&.vi {
			transform: translateX(0px);
			opacity: 0.4; } }
	// animation
	/* adaptive */
	@include below(1460px) {
		font-size: 250px; }
	@include below(960px) {
		font-size: 200px; }
	@include below(720px) {
		font-size: 100px; }
	/* adaptive */ }
.clients {
	width: 100%;
	background: #E1E7EC;
	position: relative;
	z-index: 5;
	padding-top: 275px;
	padding-bottom: 220px;
	overflow: hidden;
	/* adaptive */
	@include below(1460px) {
		padding-top: 230px; }
	@include below(960px) {
		padding-top: 30px; }
	@include below(720px) {
		padding-bottom: 150px; }
	/* adaptive */
	.container {
		flex-direction: column;
		z-index: 2; }
	&__header {
		text-align: center;
		opacity: 0;
		transition: opacity ease-in-out 1s 0s;
		position: relative;
		z-index: 5;
		&.vi {
			opacity: 1; } }
	&__backword {
		top: 35px;
		right: 100px;
		transform: translateX(230px); }
	&__slider {
		z-index: 1;
		height: 610px;
		margin-left: 162px;
		margin-right: 0;
		margin-top: 75px;
		position: relative;
		// animation
		opacity: 0;
		transition: opacity ease-in-out 1s 0s;
		&.vi {
			opacity: 1; }
		// animation
		/* adaptive */
		@include below(1460px) {
			height: auto; }
		@include below(1230px) {
			margin-left: auto; }
		@include below(960px) {
			width: 100%; }
		@include below(480px) {
			margin-top: 0px; }
		/* adaptive */
		>.swiper-container {
			width: 873px;
			margin-left: 0;
			/* adaptive */
			@include below(1460px) {
				width: 100%; }
			@include below(1230px) {
				width: 70%;
				margin-left: 15%; }
			@include below(720px) {
				width: 100%;
				margin-left: 0;
				overflow: hidden; }
			/* adaptive */ } }
	&__slide {
		position: relative;
		transform: scale(0.9);
		transition: transform ease-in-out 1s;
		&.swiper-slide-active {
			transform: scale(1); } }
	&__wrapper {
		height: 607px;
		@include below(720px) {
			height: 400px; }
		@include below(480px) {
			height: 200px; }
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } }
	&__image, &__wrapper {
		height: 100%;
		position: relative; }
	&__prx {
		height: 100%;
		position: absolute;
		top: 0%;

		// adaptive
		@include below(1230px) {
			position: static; }
 }		// adaptive
	&__description {
		position: absolute;
		left: 0;
		top: 50%;
		transform: translate(-160px, -50%);
		width: 370px;
		height: 370px;
		background: #fff;
		padding-left: 79px;
		padding-top: 102px;
		padding-right: 70px;
		// adaptive
		@include below(1230px) {
			position: static;
			transform: translate(0, 0);
			width: 100%;
			padding-top: 40px;
			padding-bottom: 40px;
			height: auto; }
		@include below(960px) {
			padding-left: 40px;
			padding-right: 40px; }
		// adaptive
		h3 {
			@include fb;
			text-transform: uppercase;
			font-weight: 300;
			font-size: 20px;
			margin-bottom: 27px;
			color: $accent; }
		p {
			width: 100%;
			font-size: 14px;
			line-height: 164%; } }
	&__arrow {
		width: 93px;
		height: 93px;
		background-color: $accent;
		@include flexbox(center, center);
		border-radius: 100%;
		cursor: pointer;
		position: absolute;
		top: 50%;
		z-index: 6;
		// adaptive
		@include below(720px) {
			top: 100%;
			width: 70px;
			height: 70px; }
		// adaptive
		&_left {
			left: 0;
			transform: translate(-300px, -50%);
			// adaptive
			@include below(1230px) {
				transform: translate(0, -50%); }
			@include below(720px) {
				transform: translate(0, 50%); }
 }			// adaptive

		&_right {
			right: 0;
			transform: translate(160px, -50%) rotate(180deg);
			// adaptive
			@include below(1230px) {
				transform: translate(0, -50%) rotate(180deg); }
			@include below(720px) {
				transform: translate(0, 50%) rotate(180deg); }
 }			// adaptive

		svg {
			font-size: 20px; } } }
.swiper-wrapper {
	align-items: center;
	// adaptive
	@include below(720px) {
		align-items: flex-start; }
 }	// adaptive
.time-manager {
	@include flexbox(flex-start, flex-start);
	padding-left: 162px;
	margin-top: 112px;
	z-index: 2;
	// animation
	opacity: 0;
	transition: opacity ease-in-out 1s 0s;
	&.vi {
		opacity: 1; }
	// animation
	// adaptive
	@include below(1230px) {
		padding-left: 0; }
	@include below(720px) {
		width: 100%;
		overflow-x: scroll;
		padding-bottom: 30px; }
	@include below(480px) {
		margin-top: 30px; }
	// adaptive
	&__item {
		font-size: 14px;
		letter-spacing: 0.56px;
		@include fsb;
		color: $black;
		margin-right: 10px;
		transition: color ease-in-out .4s;
		cursor: pointer;
		@include flexbox(center, center);
		&.active {
			color: $accent;
			.time-manager__line {
				transform: scaleX(1);
				max-width: 185px;
				// adaptive
				@include below(720px) {
					max-width: 40px; }
 } } }				// adaptive
	&__line {
		width: 185px;
		height: 1px;
		background-color: #979797;
		margin-left: 10px;
		transform: scaleX(0);
		transform-origin: left center;
		transition: transform ease-in-out .4s, max-width ease-in-out .4s;
		max-width: 0;
		// adaptive
		@include below(720px) {
			width: 40px; }
 } }		// adaptive

.partners {
	position: relative;
	margin-bottom: 3px;
	padding-top: 103px;
	padding-bottom: 140px;
	/* adaptive */
	@include below(960px) {
		padding-top: 0; }
	@include below(720px) {
		padding-bottom: 40px; }
	/* adaptive */
	.container {
		flex-direction: column;
		/* adaptive */
		@include below(960px) {
			margin-top: 100px; }
		@include below(720px) {
			margin-top: 0; }
		/* adaptive */ }

	&__header {
		color: #fff;
		text-align: center;
		margin-bottom: 78px;
		opacity: 0;
		transition: opacity ease-in-out 1s;
		/* adaptive */
		@include below(720px) {
			margin-bottom: 40px; }
		/* adaptive */
