			opacity: 0;
			transition: opacity ease-in-out 1s;
			@include durationi(500ms, 3, 0ms); }
		&.vi {
			>* {
				opacity: 1; } }
		/* animation */
		/* adaptive */
		@include below(720px) {
			flex-wrap: wrap; }
		/* adaptive */ }

	&__item {
		width: 380px;
		height: 267px;
		backdrop-filter: blur(50px);
		@include flexbox(center, center);
		margin-right: 10px;
		/* adaptive */
		@include below(960px) {
			padding-left: 30px;
			padding-right: 30px;
			&:nth-child(2) img {
				max-width: 125px; } }
		@include below(720px) {
			height: auto;
