			&:nth-child(1) img {
				max-width: 150px;
				height: auto; } }
		/* adaptive */
		&:last-child {
			margin-right: 0; }
		@include pseudo;
		img {
			z-index: 1;
			/* adaptive */
			@include below(960px) {
				width: 100%; }
			@include below(720px) {
