			opacity: 0; }
		&:hover {
			&:after {
				opacity: 1; } } } }
.about {
	position: relative;
	overflow: hidden;
	background-color: #fff;
	padding-top: 130px;
	padding-bottom: 350px;
	.container {
		z-index: 2; }
	&__container {
